.post {
  &-title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 10px;
  }

  &-info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: #999;
    margin-bottom: 10px;
  }

  &-sapo {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 16px;
  }
}

.relate-post {
  &__title {
    font-size: 18px;
    font-weight: 700;
    color: $secondary;
    border-bottom: 1px solid $border-color;
    padding-bottom: 8px;
    margin-bottom: 30px;
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 100px;
      border-bottom: 3px solid $secondary;
      position: absolute;
      bottom: -2px;
      left: 0;
    }
  }
}
