.section {
  margin-bottom: 50px;

  &__title {
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    position: relative;
    padding-bottom: 8px;
    margin-bottom: 30px;

    &::after {
      content: "";
      display: block;
      width: 250px;
      height: 1px;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      background: linear-gradient(to right, transparent, $primary, transparent);
    }

    &--white {
      color: $white;

      &::after {
        background: linear-gradient(to right, transparent, $white, transparent);
      }
    }
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 24px;
    }
  }
}

.section-2 {
  &__title {
    font-size: 18px;
    font-weight: 700;
    color: $primary;
    border-bottom: 1px solid $border-color;
    padding-bottom: 8px;
    margin-bottom: 20px;
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 100px;
      border-bottom: 3px solid currentColor;
      position: absolute;
      bottom: -2px;
      left: 0;
    }
  }
}

.aside {
  padding: 20px;
  @include shadow("xs");

  &:not(:last-child) {
    margin-bottom: 30px;
  }

  &__title {
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    border-bottom: 1px solid $border-color;
    padding-bottom: 8px;
    margin-bottom: 20px;
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 100px;
      border-bottom: 3px solid $primary;
      position: absolute;
      bottom: -2px;
      left: 0;
    }
  }
}

.aside-2 {
  &__title {
    font-size: 20px;
    position: relative;
    border-bottom: 1px solid $border-color;
    padding-bottom: 12px;
    margin-bottom: 20px;

    &::after {
      content: "";
      display: block;
      width: 90px;
      border-bottom: 3px solid $primary;
      position: absolute;
      bottom: -2px;
      left: 0;
    }
  }
}
