.video {
  position: relative;
  z-index: 1;

  &__frame {
    @include frame("rect", 70%);
  }

  &__overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    color: #333;
    transition: 0.3s;

    &:hover {
      text-decoration: none;
      background-color: rgba(#000, 0.1);
    }
  }

  &__title {
    width: 100%;
    font-size: 50px;
    font-weight: 700;
    text-transform: uppercase;
    color: $primary;
    text-align: center;
    position: absolute;
    top: 20%;
    left: 0;
    transform: translateY(-50%);

    &::after {
      content: "";
      display: block;
      width: 120px;
      max-width: 100%;
      border-bottom: 3px solid $secondary;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__icon {
    display: inline-flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 25%;
    min-height: 40px;

    img {
      display: block;
      height: 100%;
    }
  }

  &--sm &__title {
    font-size: 30px;
  }

  @media (max-width: 1199px) {
    &__title {
      font-size: 40px;
    }

    &--sm &__title {
      font-size: 30px;
    }
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 30px;
    }
  }
}

.news {
  background-color: $light;

  &__frame {
    @include frame("rect", 56.25%);
    @include frame("zoom");
    width: 55%;
    align-self: stretch;
  }

  &__body {
    padding: 16px;
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 8px;
  }

  &__info {
    color: #999;
    margin-bottom: 8px;
  }

  &__desc {
    @include max-line(6);
  }

  @media (max-width: 1199px) {
    &__title {
      font-size: 16px;
    }

    &__desc {
      @include max-line(4);
    }
  }

  @media (max-width: 575px) {
    flex-direction: column;

    &__frame {
      width: 100%;
    }
  }
}

.news-2 {
  padding-bottom: 20px;
  border-bottom: 1px solid $border-color;

  &__title {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 8px;
    @include max-line(1);
  }

  &__frame {
    @include frame("rect", 60%);
    @include frame("zoom");
    width: 150px;
    margin-right: 12px;
  }

  &__desc {
    @include max-line(3);
    margin-bottom: 4px;
  }

  &__info {
    color: #999;
  }
}

.news-3 {
  padding: 20px;
  height: 100%;
  transition: 0.4s;

  &:hover {
    @include shadow("md");
  }

  &__frame {
    @include frame("rect", 60%);
    @include frame("zoom");
  }

  &__title {
    @include max-line(2);
    font-size: 14px;
    font-weight: 700;
    margin: 16px 0 0;
  }

  @media (max-width: 575px) {
    padding: 8px;
  }
}

.news-4 {
  &__frame {
    @include frame("rect", 66.66%);
    @include frame("zoom");
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    text-align: center;
    margin: 12px 0 8px;
  }

  &__info {
    text-align: center;
    margin-bottom: 8px;
  }

  &__desc {
    text-align: center;
    @include max-line(3);
  }
}

.project {
  padding: 20px;
  transition: 0.3s;

  &:hover {
    @include shadow("md");
  }

  &__frame {
    @include frame("rect", 60%);
    @include frame("zoom");
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    color: $secondary;
    margin: 16px 0 10px;
  }

  &__info {
    padding-left: 28px;
    position: relative;

    img {
      position: absolute;
      top: 0;
      left: 0;
    }

    a {
      color: $secondary;

      &:hover {
        color: $secondary;
        text-decoration: none;
      }
    }

    & + & {
      margin-top: 4px;
    }
  }

  @media (max-width: 575px) {
    padding: 0;

    &:hover {
      box-shadow: unset;
    }

    &__title {
      font-size: 13px;
      margin: 12px 0 8px;
    }

    &__info {
      font-size: 12px;
    }
  }
}

.project-2 {
  &__frame {
    @include frame("rect", 70%);
    @include frame("zoom");
    width: 100px;
    margin-right: 15px;
    border: 1px solid $border-color;
  }

  &__title {
    font-size: 14px;
    margin: 0;
  }
}

.project-3 {
  &__frame {
    @include frame("rect", 60%);
    @include frame("zoom");
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: $primary;
    margin: 16px 0 0;
  }

  @media (max-width: 575px) {
    &__title {
      font-size: 13px;
      margin-top: 12px;
    }
  }
}

.service {
  max-width: 285px;
  margin: 0 auto;

  &__frame {
    @include frame("circle");
    @include frame("zoom");
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    margin: 20px 0 16px;
    text-align: center;
  }

  &__desc {
    @include max-line(3);
    text-align: center;
  }
}

.product {
  &__frame {
    @include frame("rect", 60%);
    @include frame("zoom");
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    text-align: center;
    margin: 20px 0 4px;
    color: #999;
  }

  &__desc {
    @include max-line(2);
    text-align: center;
    color: #999;
  }
}

.product-2 {
  &__frame {
    @include frame("rect", 100%);
    @include frame("zoom");
  }

  &__title {
    font-size: 16px;
    text-align: center;
    margin: 8px 0 0;
  }

  &__btn {
    display: inline-block;
    font-size: 14px;
    line-height: 30px;
    padding: 0 12px;
    border-radius: 4px;
    background-color: #dfdcde;
    color: $primary;
    transition: 0.3s;

    &:hover {
      color: $white;
      text-decoration: none;
      background-color: $primary;
    }
  }

  @media (max-width: 767px) {
    &__title {
      font-size: 14px;
    }
  }
}
