.banner-slider {
  position: relative;
  z-index: 1;

  &__pagination {
    position: absolute;
    bottom: 30px;
    left: 50%;
    z-index: 10;
    transform: translateX(-50%);

    .swiper-pagination-bullet {
      width: 15px;
      height: 2px;
      border-radius: 0;
      background-color: #96958e;
      transition: 0.8s;
      margin: 0 3px;

      &-active {
        width: 30px;
        background-color: $primary;
      }
    }
  }

  @media (max-width: 991px) {
    &__pagination {
      bottom: 16px;
    }
  }
}

.partner-slider {
  position: relative;
  z-index: 1;

  &__frame {
    @include frame("rect", 40%);

    img {
      object-fit: contain;
      object-position: 50% 50%;
    }
  }

  &__prev,
  &__next {
    display: flex;
    position: absolute;
    top: 50%;
    left: -60px;
    cursor: pointer;
    transform: translateY(-50%);

    &:focus {
      outline: 0;
    }

    img {
      width: 28px;
    }
  }

  &__next {
    left: auto;
    right: -60px;
  }

  @media (max-width: 575px) {
    padding: 0 30px;

    &__prev {
      left: 0;

      img {
        width: 16px;
      }
    }

    &__next {
      right: 0;

      img {
        width: 16px;
      }
    }
  }
}

.project-slider {
  @include shadow("");
  padding: 30px 60px 20px;
  border-radius: 4px;
  position: relative;
  z-index: 1;

  &__prev,
  &__next {
    display: flex;
    position: absolute;
    top: 50%;
    left: 15px;
    cursor: pointer;
    transform: translateY(-50%);

    &:focus {
      outline: 0;
    }

    img {
      width: 28px;
    }
  }

  &__next {
    left: auto;
    right: 15px;
  }

  @media (max-width: 575px) {
    padding: 16px 35px 12px;

    &__prev {
      left: 10px;

      img {
        width: 16px;
      }
    }

    &__next {
      right: 10px;

      img {
        width: 16px;
      }
    }
  }
}

.product-slider {
  position: relative;
  z-index: 1;

  &__prev,
  &__next {
    display: flex;
    position: absolute;
    top: 50%;
    left: -60px;
    cursor: pointer;
    transform: translateY(-50%);

    &:focus {
      outline: 0;
    }

    img {
      width: 28px;
    }
  }

  &__next {
    left: auto;
    right: -60px;
  }

  .swiper-slide {
    transition: 0.6s;
    transform: scale(0.8);
  }

  .swiper-slide-active {
    transform: scale(1);

    .product {
      &__title {
        color: #333;
      }

      &__desc {
        color: #333;
      }
    }
  }

  @media (max-width: 575px) {
    padding: 0 30px;

    &__prev {
      left: 0;

      img {
        width: 16px;
      }
    }

    &__next {
      right: 0;

      img {
        width: 16px;
      }
    }
  }
}

.sync-1-slider {
  margin-top: 10px;

  &__frame {
    @include frame("rect", 60%);
    background-color: $white;
  }
}

.sync-2-slider {
  margin-top: 10px;

  &__frame {
    @include frame("rect", 60%);
    background-color: $white;
  }
}
