.contact-info {
  font-size: 14px;
  line-height: 24px;
  max-width: 430px;

  a {
    color: inherit;

    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }
}

.contact-form {
  padding: 30px;
  background-color: $light;

  label {
    font-weight: 700;
  }

  @media (max-width: 757px) {
    padding: 20px;
  }
}

.contact-map {
  height: 400px;
}
