.menu {
  list-style: none;
  margin: 0;
  padding: 0;

  &-item {
    margin: 4px 0 8px;
    position: relative;
  }

  &-link {
    display: block;
    width: 100%;
    font-size: 14px;
    line-height: 40px;
    padding: 0 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: inherit;

    &:hover {
      color: inherit;
      text-decoration: none;
    }
  }

  &-toggle {
    display: inline-flex;
    width: 40px;
    height: 40px;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;

    span {
      display: inline-flex;
      width: 24px;
      height: 24px;
      justify-content: center;
      align-items: center;
      border-radius: 12px;
      transition: 0.3s;

      &:hover {
        background-color: rgba(#000, 0.1);
      }
    }

    &.active span {
      transform: rotate(90deg);
    }
  }

  &-sub {
    display: none;
  }

  &-sub &-link {
    padding-left: 40px;
  }

  &-sub-2 &-link {
    padding-left: 60px;
  }

  &-sub-3 &-link {
    padding-left: 80px;
  }

  &-item:hover > &-link {
    background-color: rgba($primary, 0.05);
  }

  &-root > &-item > &-link {
    font-weight: 700;
    text-transform: uppercase;
  }

  @media (min-width: #{$navbar-breakpoint}px) {
    // menu root
    &-root {
      display: flex;
      margin-left: 250px;
      justify-content: space-between;
      position: relative;
      z-index: 20;
    }

    &-root > &-item {
      margin: 0;

      &-mega {
        position: static !important;
      }
    }

    &-root > &-item > &-link {
      padding: 0 20px !important;
      font-size: 16px;
      line-height: 50px;
      color: $white;
    }

    &-root > &-item:hover > &-link {
      background-color: rgba(#000, 0.03);
    }

    &-root > &-item > &-toggle {
      display: none;
    }

    &-root &-sub {
      min-width: 200px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
      position: absolute;
      z-index: 2;
      top: -4px;
      left: calc(100% + 4px);
      border-radius: 4px;
      transform-origin: 0 4px;
      background-color: $white;
      padding: 4px 0;

      &::before {
        content: "";
        display: block;
        width: 8px;
        height: 100%;
        position: absolute;
        top: 0;
        right: 100%;
      }

      &-right {
        left: auto;
        right: calc(100% + 4px);
        transform-origin: 100% 0;

        &::before {
          right: auto;
          left: 100%;
        }
      }
    }

    &-root &-sub &-item {
      margin: 0;
    }

    &-root &-sub &-link {
      padding-left: 20px;
      transition: 0.3s;
      position: relative;
      z-index: 1;

      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        background-color: $primary;
        transform-origin: left;
        transform: rotateY(90deg);
        transition: 0.3s;
      }
    }

    &-root &-sub &-item:hover > &-link {
      background: 0;
      color: $white;
      font-weight: 700;

      &::before {
        transform: rotateY(0);
      }
    }

    &-root &-sub &-item:hover > &-toggle {
      color: $white;
    }

    &-root &-item-group > &-link {
      padding-right: 40px;
    }

    &-root &-item-group:hover > &-sub {
      display: block;
      animation: menu-float-show 0.2s ease-out;
    }

    &-root > &-item-group > &-sub {
      transform-origin: 0 0;
      top: calc(100% + 4px);
      left: 0;

      &::before {
        width: 100%;
        height: 8px;
        right: auto;
        left: 0;
        top: auto;
        bottom: 100%;
      }
    }
  }

  @media (min-width: #{$navbar-breakpoint}px) and (max-width: 1300px) {
    &-full-width > &-item:last-child > &-sub {
      left: auto;
      right: 0;
      transform-origin: 100% 0;
    }
  }

  @media (min-width: #{$navbar-breakpoint}px) and (max-width: 1600px) {
    &-full-width > &-item:last-child > &-sub &-sub {
      left: auto;
      right: calc(100% + 4px);
      transform-origin: 100% 4px;
    }
  }
}

.as-menu {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;

  &__item {
    border-bottom: 1px solid $border-color;
  }

  &__link {
    display: inline-block;
    color: #333;
    transition: 0.3s;
    padding: 8px 0 8px 15px;
    position: relative;
    left: 0;

    &:hover {
      color: $primary;
      text-decoration: none;
      left: 5px;
    }

    &::before {
      content: "";
      border: 5px solid transparent;
      border-left-color: #333;
      position: absolute;
      top: 12px;
      left: 0;
    }

    &.active {
      color: $primary;
    }
  }
}
