.footer {
  padding: 24px 0 20px;
  background-color: #333333;

  &__title {
    font-size: 14px;
    font-weight: 700;
    color: $white;
    text-transform: uppercase;
    margin-bottom: 12px;
  }

  &__contact {
    color: $white;

    a {
      color: $white;
      transition: 0.3s;

      &:hover {
        color: $primary;
        text-decoration: none;
      }
    }
  }

  &__map {
    height: 190px;
  }

  &__end {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }

  &__nav {
    display: flex;
    flex-wrap: wrap;
  }

  &__nav-item {
    display: inline-block;
    color: $white;
    transition: 0.3s;

    &:hover {
      color: $primary;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 15px;
      position: relative;

      &::after {
        content: "";
        display: block;
        height: 16px;
        border-right: 1px solid $border-color;
        position: absolute;
        top: 50%;
        right: -8px;
        transform: translateY(-50%);
      }
    }
  }

  &__copyright {
    color: $white;
  }
}

.f-menu {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;

  &__item {
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  &__link {
    color: $white;
    position: relative;
    left: 0;
    transition: 0.3s;

    &:hover {
      color: $primary;
      text-decoration: none;
      left: 5px;
    }
  }
}
