.sharing {
  display: flex;
  align-items: center;
  padding: 10px 0;
  border-top: 1px solid $border-color;
  border-bottom: 1px solid $border-color;

  &__label {
    color: #666;
    margin-right: 20px;
  }

  &__list {
    display: flex;
  }

  &__item {
    display: flex;

    &:hover {
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 16px;
    }

    .fa {
      display: flex;
      width: 35px;
      height: 35px;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      color: $white;
      background-color: $facebook;
    }

    .fa-twitter {
      background-color: $twitter;
    }

    .fa-pinterest {
      background-color: $pinterest;
    }

    .fa-envelope-o {
      background-color: $dark;
    }

    .fa-print {
      background-color: $light;
      color: #000;
    }
  }
}

.partners {
  padding: 30px 0 60px;

  &__title {
    font-size: 20px;
    font-weight: 700;
    text-transform: uppercase;
    border-bottom: 1px solid $border-color;
    padding-bottom: 8px;
    margin-bottom: 30px;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 20px;
  }

  &__frame {
    display: block;
    background-color: $white;
    position: relative;
    z-index: 1;
    @include shadow("xs");
    transition: 0.4s;

    &:hover {
      @include shadow("md");
    }

    &::before {
      content: "";
      display: block;
      width: 100%;
      padding-bottom: 66.66%;
    }

    img {
      width: 80%;
      height: 80%;
      object-fit: contain;
      object-position: 50% 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  @media (max-width: 991px) {
    &__grid {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  @media (max-width: 767px) {
    &__title {
      font-size: 18px;
    }

    &__grid {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  @media (max-width: 575px) {
    &__grid {
      grid-gap: 16px;
    }
  }
}

.h-project {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__desc {
    margin-bottom: 20px;
  }

  &__subtitle {
    font-size: 14px;
    font-weight: 700;
    margin-top: auto;
    margin-bottom: 8px;
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 6px 10px;
    list-style: none;
    padding-left: 0;
    margin-bottom: 16px;

    li {
      padding-left: 20px;
      position: relative;
      left: 0;
      transition: 0.3s;

      &:hover {
        left: 8px;
      }

      &::before {
        content: "";
        display: block;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: $primary;
        position: absolute;
        top: 8px;
        left: 0;
      }
    }

    a {
      color: #333;
      transition: 0.3s;

      &:hover {
        color: $primary;
        text-decoration: none;
      }
    }
  }
}

.more {
  display: inline-flex;
  align-items: center;
  color: $primary;
  text-transform: uppercase;

  &:hover {
    color: $primary;
    text-decoration: none;
  }

  img {
    margin-left: 16px;
  }
}

.pd-tabs {
  display: block;
  text-align: center;
  white-space: nowrap;
  overflow: auto;

  .nav-item {
    display: inline-block;

    &:not(:last-child) {
      margin-right: 15px;
    }
  }

  .nav-link {
    font-size: 14px;
    line-height: 33px;
    padding: 0 16px 0 30px;
    position: relative;
    border-radius: 18px;
    border: 1px solid $border-color;
    color: #333;

    &:hover {
      color: #333;
      text-decoration: none;
    }

    &::after {
      content: "";
      display: block;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background-color: $primary;
      position: absolute;
      top: 50%;
      left: 15px;
      transform: translate(-50%, -50%);
    }

    &.active {
      background-color: $primary;
      border-color: $primary;
      color: $white;

      &::after {
        background-color: $white;
      }
    }
  }
}

.home-about {
  background: url("../images/home-about-bg.jpg") center/cover no-repeat;
  position: relative;
  z-index: 1;
  padding: 30px 0 0;
  margin-bottom: 40px;

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgba($primary, 0.85);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }

  &__content {
    color: $white;

    a,
    a:hover {
      color: $white;
      text-decoration: none;
    }
  }
}

.about {
  padding: 40px 0 60px;

  &__title {
    font-size: 36px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 24px;
  }

  &__video {
    background: url("../images/about-video-bg.png") center/contain no-repeat;
    background-position: 50% 100%;
    margin-bottom: 30px;

    .video {
      max-width: 360px;
      margin: 0 auto;
    }
  }

  &__desc {
    text-align: center;
    margin-top: -16px;
    margin-bottom: 24px;
  }

  hr {
    border-color: $primary;
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 24px;
    }
  }
}

.detail-tabs {
  .nav-link {
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    padding: 15px 25px;
    border-radius: 4px 4px 0 0;
    background: 0;
    border: 0;
    color: #333;

    &:hover {
      color: $primary;
      text-decoration: none;
    }

    &.active {
      background-color: $primary;
      color: $white;

      &:hover {
        color: $white;
      }
    }
  }

  @media (max-width: 767px) {
    .nav-item {
      flex-grow: 1;
    }

    .nav-link {
      display: block;
      text-align: center;
      padding: 8px 12px;
    }
  }
}

.detail-content {
  border: 1px solid $border-color;
  padding: 30px;

  @media (max-width: 767px) {
    padding: 20px 16px;
  }
}

.pd-info {
  &__title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 16px;
  }

  &__content {
    margin-bottom: 24px;

    ul {
      padding-left: 17px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.pj-detail {
  margin: 10px 0 24px;

  table {
    td {
      background-color: $border-color;
      padding: 8px 20px;
      border-color: $white;

      &:first-child {
        background-color: $secondary;
        color: $white;
        white-space: nowrap;
      }

      a {
        color: inherit;

        &:hover {
          color: $primary;
        }
      }
    }

    tr:first-child td {
      border-top: 0;
    }
  }

  @media (max-width: 575px) {
    table {
      td {
        font-size: 12px;
        padding: 6px 12px;
      }
    }
  }
}
