.logo {
  display: inline-flex;
  margin: 10px 0;

  img {
    height: 50px;
  }
}

.header {
  position: relative;
  z-index: 100;

  &__wrapper {
    background-color: $white;
  }

  &__inner {
    display: flex;
    align-items: center;
    position: relative;
  }

  &__elements {
    display: flex;
    align-items: center;
    margin-left: auto;
  }

  &__btn {
    display: inline-flex;
    width: 35px;
    height: 35px;
    justify-content: center;
    align-items: center;
    position: relative;
    color: $gray-800;
    font-size: 20px;

    & + & {
      margin-left: 4px;
    }

    &:hover {
      color: $primary;
      text-decoration: none;
    }

    span {
      display: inline-flex;
      width: 15px;
      height: 15px;
      justify-content: center;
      align-items: center;
      background-color: $primary;
      border-radius: 50%;
      font-size: 11px;
      font-weight: 700;
      text-align: center;
      color: $white;
      position: absolute;
      top: 0;
      right: 0;
    }

    img {
      max-height: 16px;
      object-fit: contain;
      object-position: 50% 50%;
    }
  }

  &.is-fixed &__wrapper {
    width: 100%;
    @include shadow("xs");
    position: fixed;
    top: 0;
    left: 0;
    animation: fade-in 0.3s ease-in-out;
  }

  @media (min-width: 1200px) {
    &__inner {
      height: 62px;
    }

    &__logo {
      display: flex;
      align-items: center;
      height: 112px;
      margin: 0;
      position: absolute;
      z-index: 10;
      top: 0;
      left: 0;

      img {
        height: 80px;
      }

      &::after {
        content: "";
        display: block;
        width: 180%;
        height: 100%;
        background: linear-gradient(
          to right,
          transparent,
          rgba(#fff, 1),
          transparent
        );
        position: absolute;
        z-index: -1;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    }

    &__btn {
      display: none;
    }

    &.is-fixed &__inner {
      height: 0;
    }

    &.is-fixed .lang {
      top: 25px;
      transform: translateY(-50%);

      &__item {
        &:not(:last-child) {
          margin-right: 12px;
        }

        img {
          height: 20px;
        }
      }
    }

    &.is-fixed .logo {
      height: 50px;

      img {
        height: 40px;
      }
    }

    &.is-fixed .menu-root {
      margin-left: 140px;
      margin-right: 100px;
    }
  }
}

.navigation {
  &__inner {
    position: relative;
  }

  &__slogan {
    text-align: center;
    padding: 0 0 10px;
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    color: $secondary;
  }

  @media (min-width: 767px) {
    &__slogan {
      margin: 0;
      padding: 0;
      position: absolute;
      top: -35px;
      left: 155px;
      transform: translateY(-50%);
    }
  }

  @media (min-width: 1200px) {
    background-color: $primary;

    &__slogan {
      top: -31px;
      font-size: 26px;
      left: 260px;
    }
  }
}

.header.is-fixed .navigation__slogan {
  display: none;
}

.search {
  padding: 10px 20px;

  .form-control {
    border-right: 0;

    &:focus {
      border-color: #ddd;
    }
  }

  .input-group-text {
    background-color: $white;
    border-left: 0;
  }

  @media (min-width: 1200px) {
    width: 166px;
    padding: 0;
    margin: 0;
    position: absolute;
    top: -37px;
    right: 0;
    border-bottom: 1px solid $primary;

    .form-control {
      border: 0;
      padding: 0;
      height: 25px;
      color: $primary;
      font-size: 12px;

      &::placeholder {
        color: $primary;
      }
    }

    .input-group-text {
      border: 0;
      color: $primary;
      padding: 0 0 0 4px;
    }
  }
}

.hotline {
  display: block;
  padding: 10px 20px;
  margin-bottom: 10px;
  color: $primary;
  font-size: 14px;
  font-weight: 700;

  &:hover {
    color: $primary;
    text-decoration: none;
  }

  @media (min-width: 1200px) {
    position: absolute;
    top: -62px;
    right: 56px;
    padding: 0;
    margin: 0;
    line-height: 25px;
  }
}

.lang {
  display: flex;

  &__item {
    display: flex;

    &:not(:last-child) {
      margin-right: 8px;
    }

    img {
      height: 16px;
    }
  }

  @media (min-width: 1200px) {
    position: absolute;
    top: 6px;
    right: 0;

    &__item {
      &:not(:last-child) {
        margin-right: 5px;
      }

      img {
        height: 13px;
      }
    }
  }
}
